<template>
  <el-dialog title="修改状态" :visible.sync="show" center width="400px" @close="resetForm('statusForm')" :close-on-click-modal="false">
    <el-form :model="statusForm" :rules="rules" ref="statusForm" class="statusForm">
      <el-form-item>
        <p class="title">协议标题：{{dataInfo.agreementName||EMPTYTEXT}}</p>
      </el-form-item>
      <el-form-item prop="status">
        <el-radio-group v-model="statusForm.status">
          <el-radio v-for="item in statusOption" :key="item.value" :label="item.value">{{item.label}}</el-radio>
        </el-radio-group>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="resetForm('statusForm')">取 消</el-button>
      <el-button type="primary" @click="submitForm('statusForm')">确 认</el-button>
    </div>
 </el-dialog>
</template>

<script>
export default {
  name: 'change-status',
  props: {
    value: {
      type: Boolean
    },
    dataInfo:{
      type:Object,
      default: ()=>{}
    }
  },
  model: {
    prop: 'value',
    event: 'change'
  },
  watch: {
    value (n) {
      this.show = this.value
      if(this.show === true){
        this.statusForm.status = this.dataInfo.agreementStatus!==''?this.dataInfo.agreementStatus:''
      }
    }
  },
  data () {
    return {
       show:this.value,
       statusForm:{
         status:''
       },
       statusOption:[
         {
           value:1,
           label:'已启用'
         },
         {
           value:2,
           label:'已禁用'
         },
       ], //状态下拉
       rules: {
          status: [
            { required: true, message: '请选择状态', trigger: 'change' }
          ]
      }
    }
  },
  methods: {
    //保存设置
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          let params = {}
          params.templateId = this.dataInfo.templateId
          params.agreementStatus = this.statusForm.status
          this.$http.post(process.env.VUE_APP_API+'/family/agreementTemplate/updateStatus',params).then(res => {
            if(res.code===200){
              this.$message.success('操作成功');
              this.show = false
              this.$emit('change', false)
              this.$emit('refresh') //刷新列表
              this.$refs[formName].resetFields();
            }else{
              this.$message.error(res.msg);
            }
          })
          .catch(error => {
            this.$message.error(error);
          });
        }
      });
    },
    //取消设置
    resetForm(formName) {
      this.show = false
      this.$emit('change', false)
      this.$refs[formName].resetFields();
    }
  }
}
</script>

<style lang='less' scoped>
  /* 修改状态弹窗 */
  .statusForm{
    padding:0px 15px;
   .title{
     	font-size: 22px;
      color: #746651;
   }
  }
</style>