<!-- 协议管理列表 -->
<template>
  <div class="list-common-box argument-list-box">
    <!-- 标题 -->
    <div class="title-box">
      <Breadcrumb :isLevel='2'></Breadcrumb>
    </div>
    <!-- 主要数据 -->
    <div class="card-box">
      <!-- 搜索 -->
      <div class="search-bar">
        <!-- 左 -->
        <div class="search-left">
          <div class="search-item">
            <span class="search-label">协议标题</span>
            <el-input v-model.trim="searchKey.agreementName" placeholder="请输入协议标题" clearable></el-input>
          </div>
          <div class="search-item">
            <span class="search-label">协议类型</span>
            <el-select placeholder="请选择协议类型" v-model="searchKey.agreementType" clearable>
              <el-option v-for="item in agreementTypeOption" :key="item.value" :label="item.label" :value="item.value">
              </el-option>
            </el-select>
          </div>
        </div>
        <div class="search-right">
          <el-button type="primary" plain icon="el-icon-search" @click="querylist()">查询</el-button>
          <el-button type="primary" plain icon="el-icon-delete" @click="Refreshlist()">清空</el-button>
        </div>
      </div>
      <!-- 表格 -->
      <div class="table-box">
        <el-table :data="dataList" tooltip-effect="dark" stripe height="100%"> 
          <el-table-column label="序号" align="center" width="80">
            <template slot-scope="scope">
              {{scope.$index+(pageNum - 1) * pageSize + 1}}
            </template>
          </el-table-column>
          <el-table-column prop="agreementName" label="协议标题" align="center">
            <template slot-scope="scope">
              {{scope.row.agreementName?scope.row.agreementName:EMPTYTEXT}}
            </template>
          </el-table-column>
          <el-table-column prop="agreementTypeStr" label="协议类型" align="center">
            <template slot-scope="scope">
              {{scope.row.agreementTypeStr?scope.row.agreementTypeStr:EMPTYTEXT}}
            </template>
          </el-table-column>
          <el-table-column prop="teamIsFirstStr" label="甲方或乙方" align="center">
            <template slot-scope="scope">
              {{scope.row.teamIsFirstStr?scope.row.teamIsFirstStr:EMPTYTEXT}}
            </template>
          </el-table-column>
          <el-table-column prop="registerOrganization" label="机构" align="center">
            <template slot-scope="scope">
              {{scope.row.registerOrganization?scope.row.registerOrganization:EMPTYTEXT}}
            </template>
          </el-table-column>
          <el-table-column prop="agreementStatus" label="状态" align="center">
            <div slot-scope="scope" class="link" @click="handleStatus(scope.row)">
              {{scope.row.agreementStatus?statusOption[scope.row.agreementStatus]:EMPTYTEXT}}
              <cc-svg-icon icon-class="yellowEdit" v-if="scope.row.agreementStatus"></cc-svg-icon>
            </div>
          </el-table-column>
          <el-table-column prop="createDateStr" label="最后修改时间"  align="center" width="170">
            <template slot-scope="scope">
              {{scope.row.createDateStr?scope.row.createDateStr:EMPTYTEXT}}
            </template>
          </el-table-column>
          <el-table-column label="操作"  align="center" width="120">
            <template slot-scope="scope">
              <el-button type="primary" @click="handleAdd(scope.row.templateId)" v-if="!scope.row.agreementStatus">新增协议</el-button>
              <el-button type="primary" @click="handleDetail(scope.row)" class="green" v-else>查看详情</el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <!-- 分页 -->
      <div class="page-box">
         <Paginaction class='pagination' :goPage="pageNum" :totalNum="totalNum" @updateList="changePage" ref="paginationEvent"/>
      </div>
    </div>
     <!-- 改变状态弹窗 -->
     <changeStatus v-model="statusDialog" :dataInfo="agreementInfo" @refresh="getDatalist"></changeStatus>
  </div>
</template>

<script>
  import changeStatus from './changeStatus' //修改状态
  export default {
    name: 'agreement-manage-list',
    components: {
      changeStatus
    },
    data() {
      return {
        searchKey:{
          agreementName:'',//协议名称
          agreementType:''//协议类型
        },
        agreementTypeOption:[
          {
            value: 0,
            label: '全部'
          },
          {
            value: 1,
            label: '签约协议'
          },
          {
            value: 2,
            label: '解约协议'
          }
        ],
        statusOption:{
          1:"已启用",
          2:'已禁用'
        },
        pageNum:1,//当前页
        pageSize:10,//每页条数
        totalNum: 0, //总条数
        dataList: [], //协议列表
        statusDialog:false,//修改状态弹窗
        agreementInfo:{},//协议信息
      }
    },
    created() {
      this.getDatalist()
    },
    methods: {
      //修改协议信息
      handleAdd(id){
        this.$router.push({
          path: '/system-manage/agreement-manage/add',
          query:{id:id,status:'add'}          
        })
      },
      //查看详情
      handleDetail(scope) {
        this.$router.push({
          path: '/system-manage/agreement-manage/detail?id=' + scope.templateId
        })
      },
      //修改状态弹窗
      handleStatus(scope){
        this.statusDialog = true
        this.agreementInfo = scope?scope:{}
      },
      //查询
      querylist() {
        this.pageNum = 1
        this.getDatalist()
      },
      //重置
      Refreshlist() {
        this.pageNum = 1
        this.pageSize = 10
        Object.keys(this.searchKey).forEach(k => this.searchKey[k] = '')
        this.getDatalist()
      },
      //分页改变
      changePage(query){
        this.pageNum = query.pageNum
        this.pageSize = query.pageSize
        this.getDatalist()
      },
      //获取协议列表
      getDatalist(query) {
        this.$emit("viewLoad", true);
        let params = {}
        params.pageNum  = this.pageNum
        params.pageSize = this.pageSize
        params = Object.assign({},params,this.searchKey)
        this.$http.post(process.env.VUE_APP_API + '/family/agreementTemplate/getByPage', params).then(res => {
          if (res.code===200) {
            this.dataList = res.data.list || [] //协议列表
            this.totalNum = res.data.total //总条数
            this.pageNum = res.data.pageNum //当前页
            this.pageSize = res.data.pageSize //每页条数
          } else {
            this.$message.error(res.msg);
          }
          this.$emit("viewLoad", false);
        })
        .catch(error => {
          this.$emit("viewLoad", false);
          this.$message.error(error);
        });
      }
    }
  }
</script>
<style lang='less' scoped>
</style>